import {UserModel} from "../../model/user";
const  userModel = new UserModel()
export default {
    name: "payment_manage",
    data(){
        return {
            password:'',
            confirm_password:''
        }
    },
    methods:{
        _password(e)
        {
            this.password = e.detail.value
        },
        _confirm_password(e)
        {
            this.confirm_password = e.detail.value
        },
        finish(){
            if (this.password != this.confirm_password) {
                uni.showToast({
                    title:'两次密码不一致,请重新输入',
                    icon:'none'
                })
                return false;
            }

            let params = {
                new_password : this.password,
                type:1
            }
            userModel.setPassword(params).then((res) => {
                    uni.showToast({
                        title:res.message,
                        icon:'none'
                    })
                    if (res.code == 200) {
                        uni.navigateBack()
                    }
                    
            })
        }
    }

}   
